
module.exports = {
	
	// api  url
	// apiBaseUrl        : 'https://useful.youyong.org.cn/',
	apiBaseUrl        : 'http://192.168.1.125:68/',
	// apiBaseUrl        : 'https://test.youyong.org.cn/',
	
	//  [ false  false ]
	debug             : false,
	
	//  Token 
	localTokenKeyName : 'reqToken',
	
	//  Token 
	userTokenKeyName  : 'uToken',
	
	// token ,   
	expiredTime       : 7200,
	
	// post  header[content-type] 
	postHeaderDefault : 'application/x-www-form-urlencoded',
	
	//  token 
	//  api  token 
	// 	
	getToken : (resolve, reject) => { 
		
		//  
		//  : 
		var appKey = 'useful@2022%**';
		
		// 
		uni.request({
			// token  api 
			// url      : "https://useful.youyong.org.cn/swoft/api/token/getReqToken",
			url      : "http://192.168.1.125:68/swoft/api/token/getReqToken",
			// url      : "https://test.youyong.org.cn/swoft/api/token/getReqToken",
			// 
			data     : { 'appKey' : appKey },
			//  POST
			method   : "POST",
			// headers
			header   : {'content-type' : 'application/x-www-form-urlencoded'},
			// 
			dataType : "json",
			// 
			success  : (res) => {
				var data = res.data;
				if(data.code == 0){
					resolve(data.data);
				}else{
					reject();
				}
			},
			// 
			fail     : (e) => { reject();}
		});
	}
	
}
